
import { defineComponent, ref, reactive, onMounted, watch } from 'vue';
export default defineComponent({
  setup() {
    const formRef = ref();
    const form = reactive({
      name: '',
      avatar: '',
    });
    const isDisabled = ref();
    const imageUrl = ref();
    const rules = {
      name: [
        { required: true, message: '請輸入帳號', trigger: 'blur' },
        { min: 3, max: 15, message: '長度在3-15之間', trigger: 'blur' },
      ],
    };

    const formValidate = () => {
      formRef.value.validate((valid: boolean) => (isDisabled.value = !valid));
    };

    onMounted(() => {
      formValidate();
    });

    watch(form, () => formValidate(), { deep: true });

    const handleAvatarSuccess = (res: Response, file: File) => {
      imageUrl.value = URL.createObjectURL(file);
      form.avatar = imageUrl.value;
    };
    const beforeAvatarUpload = (file: File) => {
      console.log('file', file);

      const isJPG = file.type === 'image/jpeg';
      const isLt2M = file.size / 1024 / 1024 < 2;
    };

    const submitForm = () => {
      return;
    };
    const resetForm = () => {
      return;
    };

    return {
      form,
      formRef,
      rules,
      imageUrl,
      beforeAvatarUpload,
      handleAvatarSuccess,
      isDisabled,
      submitForm,
      resetForm,
    };
  },
});
